  color: #000000;
  font-weight: bold;
  font-size: 14px;
  line-height: 26px;
  text-align: center;
}
.site-list__link:hover {
  color: #81B3D2;
}
.site-list__link:focus {
  color: #000000;
  opacity: 0.3;
}
.site-list__link:active {
  color: #766357;
}
@media (max-width: 767px) {
  .main-nav__wrapper {
    min-width: 320px;
    width: 100%;
    min-height: 320px;
    padding: 0;
  }
  .main-nav__list {
    display: flex;
    flex-direction: column;
    align-items: stretch;
  }
  .site-list__link {
    display: block;
    background-color: #81B3D2;
    color: #ffffff;
    min-width: 320px;
    height: 55px;
    width: 100%;
    margin: 0;
    padding-top: 10px;
    font-size: 18px;
    line-height: 40px;
    font-weight: bold;
    border-top: 1px solid #9AC2DB;
    border-bottom: 1px solid #678FA8;
  }
  .site-list__link:hover {
    color: #ffffff;
    background-color: #669EC0;
  }
  .site-list__link:focus {
    color: rgba(255, 255, 255, 0.3);
    opacity: 1;
    background-color: #5496BD;
  }
  .page-header__logo {
    order: 3;
    display: block;
    min-width: 320px;
    text-align: center;
  }
  .main-nav__toggle {
    position: absolute;
    top: 222px;
    right: 2px;
    z-index: 2;
    display: block;
    width: 70px;
    height: 50px;
    cursor: pointer;
    border: none;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 23px;
    height: 4px;
    background-color: #81B3D2;
    box-shadow: 0 9px 0 0 #81B3D2, 0 18px 0 0 #81B3D2;
  }
  .main-nav--closed .main-nav__toggle:hover::before {
    background-color: rgba(129, 179, 210, 0.6);
    box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.6), 0 18px 0 0 rgba(129, 179, 210, 0.6);
  }
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: rgba(129, 179, 210, 0.3);
    box-shadow: 0 9px 0 0 rgba(129, 179, 210, 0.3), 0 18px 0 0 rgba(129, 179, 210, 0.3);
  }
  .main-nav--closed .site-list__item {
    visibility: hidden;
  }
  .main-nav--opened .main-nav__toggle {
    top: 0;
    right: 10px;
    left: auto;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 24px;
    right: 18px;
    width: 28px;
    height: 4px;
    background-color: #ffffff;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:hover::before,
  .main-nav--opened .main-nav__toggle:hover::after {
    background-color: rgba(255, 255, 255, 0.6);
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    min-width: 768px;
    width: 100%;
  }
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}
