.main-nav {
  background-color: @basic-white;
    position: relative;
}

.main-nav__wrapper {
    max-width: 1280px;
    min-width: 320px;
    margin: 0;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
//    outline: 5px solid red;
    outline-offset: -5px;
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  justify-content: center;
  align-items: flex-start;
  display: flex;
//  display: grid;
//  grid-template-columns: 200px 200px 400px 200px 200px;
//    grid-template-columns: 1fr 1fr 2fr 1fr 1fr;
//  grid-template-areas: 50px;
}

site-list__item {
    display: block;
    z-index: 2;
//    margin-top: 25px;
}

.site-list__link {
  display: block;
  margin-top: 15px;
  color: @basic-black;
  font-weight: bold;
  font-size: 14px;
  line-height: 26px;
  text-align: center;
//  margin-top: 15px;
//  vertical-align: middle;
//  padding-top: 25px;
}

.site-list__link:hover {
    color: @basic-blue;
}

.site-list__link:focus {
    color: @basic-black;
    opacity: 0.3;
}

.site-list__link:active {
    color: @basic-brown;
}

@media (max-width: @mobile-width-only) {
    .main-nav--closed .site-list__item {
//        display: none;
//        visibility: hidden;
    }
    .main-nav__wrapper {
        min-width: 320px;
        width: 100%;
//        width: 320px;
        min-height: 320px;
        padding: 0;
    }
    .main-nav__list {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        
        
    }
    .site-list__link {
        display: block;
        background-color: @basic-blue;
        color: @basic-white;
        min-width: 320px;
        
//        text-align: center;
//        flex-grow: 1;
        height: 55px;
        width: 100%;
        margin: 0;
//        padding: 10px auto 5px auto;
        padding-top: 10px;
        font-size: 18px;
        line-height: 40px;
        font-weight: bold;
        border-top: 1px solid #9AC2DB;
        border-bottom: 1px solid #678FA8;
//        box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 1px 0px rgba(255, 255, 255, 0.2);
    }
    .site-list__link:hover {
        color: @basic-white;
        background-color: @deep-blue;
    }
    .site-list__link:focus {
        color: rgba(@basic-white, 0.3);
        opacity: 1;
        background-color: @indigo-blue;
    }
    .page-header__logo {
        order: 3;
        display: block;
        min-width: 320px;
        text-align: center;  
    }
    .main-nav__toggle {
        position: absolute;
        top: 222px;
        right: 2px;
        z-index: 2;
        display: block;
        width: 70px;
        height: 50px;
//        background-color: #6a4d38;
        cursor: pointer;
        border: none;

        &:hover,
        &:active {
//        background-color: #624530;
        }
    }
    .main-nav--closed .main-nav__toggle::before {
        content: "";
        position: absolute;
        top: 15px;
        left: 17px;
        width: 23px;
        height: 4px;
        background-color: @basic-blue;
        box-shadow: 0 9px 0 0 @basic-blue, 0 18px 0 0 @basic-blue;
    }
    .main-nav--closed .main-nav__toggle:hover::before {
        background-color: fade(@basic-blue, 60%);
        box-shadow:
            0 9px 0 0 fade(@basic-blue, 60%),
            0 18px 0 0 fade(@basic-blue, 60%);
    }
    .main-nav--closed .main-nav__toggle:active::before {
        background-color: fade(@basic-blue, 30%);
        box-shadow:
            0 9px 0 0 fade(@basic-blue, 30%),
            0 18px 0 0 fade(@basic-blue, 30%);
    }
    .main-nav--closed .site-list__item {
//        display: none;
        visibility: hidden;
    }
    .main-nav--opened .main-nav__toggle {
        top: 0;
        right: 10px;
        left: auto;

        &::before,
        &::after {
            content: "";
            position: absolute;
            top: 24px;
            right: 18px;
//            width: 36px;
            width: 28px;
            height: 4px;
            background-color: #ffffff;
        }

        &::before {
            transform: rotate(45deg);
            box-shadow: none;
        }

        &::after {
            transform: rotate(-45deg);
        }
        
        &:hover::before,
        &:hover::after {
            background-color: fade(#ffffff, 60%);
        }

        &:active::before,
        &:active::after {
            background-color: fade(#ffffff, 30%);
        }
    }
}

@media (min-width: @tablet-width) {
    .main-nav__wrapper {
        min-width: 768px;
        width: 100%;
    }
    .main-nav__list {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }
}