.description {
    text-align: center;
    padding-top: 36px;
    padding-bottom: 16px;
//    margin-bottom: 60px;
}


.description__title {
    font-weight: bold;
    font-size: 21px;
    margin: 0;
    padding-bottom: 26px;
}

.description__text {
    line-height: 24px;
    margin: 0;
}

@media (max-width: @mobile-width-only) {
    .description {
        min-width: 320px;
        padding-left: 20px;
        padding-right: 20px;
    }
    .description__break--tablet {
        display: none;
    }
}

@media (min-width: @tablet-width) {
    .description {
        margin-bottom: 60px;
    }
    .description__break--mobile {
        display: none;
    }
}