.form-review {
    padding-top: 20px;
    margin-bottom: 48px;
}

@media (max-width: @mobile-width-only) {
    .form-review {
    min-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
    }
    .form-review__comment {
        display: none;
    }
}

@media (min-width: @tablet-width) {
    .form-review {
    min-width: 768px;
    padding-left: 60px;
    padding-right: 60px;
    }
}

@media (min-width: @desktop-width) {
    .form-review {
    min-width: 1200px;
    padding-left: 160px;
    padding-right: 160px;
    }
}

@media (min-width: @tablet-width) {
    .form-review__form {
        display: grid;
//        grid-template-rows: 1fr 1fr;
//        grid-template-columns:
//            auto
//            auto
//            auto
//            auto;
        
        grid-template-areas:
            "form-review__item--feelings form-review__item--name"
            "form-review__item--contacts form-review__item--contacts"
            "form-review__item--attraction form-review__item--attraction"
            "form-review__item--feedback form-review__item--feedback";
        grid-template-columns: 1fr 1fr;
//        column-gap: 30px;
    }
    .form-review__item--feelings {
        grid-area: form-review__item--feelings;
    }
    .form-review__item--name {
        grid-area: form-review__item--name;
    }
    .form-review__item--contacts {
        grid-area: form-review__item--contacts;
    }
    .form-review__item--attraction {
        grid-area: form-review__item--attraction;
    }
    .form-review__item--feedback {
        grid-area: form-review__item--feedback;
    }
}

.form-group__title, .form-list__title, .form-feedback__title {
    text-align: center;
}

