
.packshot__image {
    margin-top: -25px;
    z-index: 1;
    position: relative;
    width: 100vw;
    object-fit: cover;
}

.packshot img {
    object-fit: cover;
    width: 100vw;
}

.packshot::after {
    content: "";
    position: absolute;
    width: 458px;
    height: 214px;
    background-image: url("../img/Shape.png");
    z-index: 2;
    top: 265px;
    left: 520px;
}

@media (min-width: @tablet-width) {
    .packshot {
        width: 100%;
        position: relative;
    }
    .packshot::after {
        content: "";
        position: absolute;
        width: 461px;
        height: 350px;
        background-image: url("../img/text-index.svg.svg");
        margin-left: auto;
        margin-right: auto;
        z-index: 2;
        top: 77px;
        left: 0;
        right: 0;
    }
    .packshot::before {
        display: none;
    }
    .benefits__wrapper::before {
        display: none;
    }
    .packshot img {
//        display: none;
    }
    .packshot__image {
        display: block;
        background-image: url("../img/background_photo.webp");
//        background-image: srcset="../img/background_photo.webp, ../img/background_photo.png";
        background-position: 50% 50%;
        background-repeat: no-repeat;
        width: 768px;
        height: 509px;
        object-fit: cover;
        width: 100%;
        
        @media (min-resolution: 2dppx) {
            background-image: url("../img/background_photo@2x.webp");
//            background-position: 50% 20%;
//            width: 320px;
//            height: 389px;
//            background-size: 1338px auto;
            background-size: 1200px auto;
        }
    }
    .packshot--inner {
        width: 100%;
        position: relative;
        margin-bottom: 65px;
    }
    .packshot__image--inner {
        position: relative;
        display: block;
        background-image: url("../img/background_photo.webp");
        background-position: 50% 85%;
        background-repeat: no-repeat;
        height: 290px;
        object-fit: cover;
        width: 100%;
        border: none;
        outline: none;
        margin-top: -45px;
        
        @media (min-resolution: 2dppx) {
            background-image: url("../img/background_photo@2x.webp");
//            background-position: 50% 20%;
//            width: 320px;
//            height: 389px;
//            background-size: 1338px auto;
            background-size: 1200px auto;
        }
    }
    .packshot--inner::before {
        content: "";
        position: absolute;
        width: 457px;
        height: 78px;
        background-image: url("../img/SEDONA_BIG.svg");
        background-repeat: no-repeat;
        object-fit: cover;
        margin-left: auto;
        margin-right: auto;
        z-index: 2;
        top: 95px;
        left: 0;
        right: 0;
    }
    .packshot--inner::after {
        content: "";
        position: absolute;
        width: 100%;
        height: 57px;
        background-image: url("../img/intro-triangle.svg");
        background-position: 50% 50%;
        z-index: 2;
        bottom: 0;
        left: 0;
        right: 0;
    }
}



