.checkbox__input:not(:last-child) {
    margin-bottom: 54px;
}
.checkbox {
    margin-top: 20px;
    margin-bottom: 40px;
}
.checkbox label {
    position: relative;
    padding-left: 70px;
    padding-top: 12px;
}
.checkbox__input:not(:last-child) {
    margin-bottom: 43px;
}
.checkbox__input + label:before {
    content: "";
    position: absolute;
    left: 0px;
    top: 0px;
    width: 46px;
    height: 46px;
    background-color: @light-beige;
}
.checkbox .checkbox__input:checked + label:after {
    content: "";
    position: absolute;
    top: 16px;
    left: 12px;
    width: 24px;
    height: 17px;
    background-image: url("../img/tick.svg");
}
