.field {
    position: relative;
}
.field--login {
    width: 100%;
    display: flex;
}
.field__label {
    padding-top: 20px;
}
.field__input {
  background-color: @light-beige;
  border: none;
  padding: 18px 22px 14px 22px;
  margin-bottom: 20px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: bold;
  font-size: 18px;
  line-height: 26px;
    flex-grow: 1;
    margin-left: 20px;
}
.field__input::placeholder {
  color: @basic-black;
}
.field--contacts {
//    display: block;
    position: relative;
}
.field--contacts::after {
    content: "";
    position: absolute;
    width: 58px;
    height: 58px;
    background-color: @dark-beige;
    background-image: url("../img/phone.svg");
    background-repeat: no-repeat;
//        background-image: url("../img/intro-triangle.svg");
    background-position: 50% 50%;
    z-index: 2;
    top: 26px;
//        bottom: 0;
    right: 0;
}
.field--contacts-mail::after {
    content: "";
    position: absolute;
    width: 58px;
    height: 58px;
    background-color: @dark-beige;
//    background-image: url("../img/phone.svg");
    background-repeat: no-repeat;
//        background-image: url("../img/intro-triangle.svg");
    background-position: 50% 50%;
    z-index: 2;
    top: 26px;
//        bottom: 0;
    right: 0;
    background-image: url("../img/mail.svg");
}
.field__input--telephone, .field__input--email {
    width: 100%;
    margin-left: 0;
}