.cost {
    border: none;
//    outline: 5px solid red;
    margin-left: 200px;
}

.cost__title {
    font-size: 16px;
    line-height: 21px;
    font-weight: bold;
    padding-top: 26px;
//    padding-left: 50px;
    text-align: start;
}

.cost__number {
    text-align: start;
    border: 2px solid @basic-white;
    border-radius: 2px;
    width: 317px;
    height: 36px;
}

.cost__button {
    margin-top: 50px;
    padding-top: 3px;
    width: 137px;
    height: 36px;
    background-color: transparent;
    color: @basic-white;
    border: 2px solid @basic-white;
    border-radius: 2px;
    text-transform: uppercase;
}