.radio {
    margin-top: 20px;
    margin-bottom: 40px;
}
.radio label {
    position: relative;
    padding-left: 70px;
    padding-top: 12px;
}
.radio__input:not(:last-child) {
    margin-bottom: 43px;
}
.radio__input + label:before {
    content: "";
    position: absolute;
    left: 0px;
    top: 0px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: @light-beige;
}
.radio .radio__input:checked + label:after {
    content: "";
    position: absolute;
    top: 15px;
    left: 15px;
    width: 18px;
    height: 18px;
    background-color: #000000;
    background-image: none;
    border-radius: 50%;
}