.placement__list {
    padding: 0;
    margin: 0;
//    outline: 5px solid green;
    list-style: none;
    display: grid;
    grid-template-columns: 1200px;
    grid-template-areas:
        151px
        151px
        151px;
}

.placement__item {
    display: grid;
    grid-template-columns: 210px 300px 690px;
//    grid-template-areas: 151px;
//    width: 1200px;
    height: 151px;
//    height: 100vh;
//    padding: 0;
//    margin: 0;
    border-top: 1px solid @basic-beige;
    border-bottom: 1px solid @basic-beige;
}

.placement__item:nth-child(2) {
    border: none;
}

.placement__picture {
    margin-left: 73px;
    margin-top: auto;
    margin-bottom: auto;
}

.placement__info {
//    outline: 2px solid red;
//    margin-left: 50px;
//    display: flex;
//    grid-template-columns:
//    grid-template-areas:
}

.placement__title {
    font-size: 21px;
    font-weight: bold;
    text-align: start;
    margin-left: 20px;
    margin-top: 24px;
//    outline: 2px solid green;
    margin-bottom: 10px;
}

.placement__title:hover {
    color: @basic-blue;
}

.placement__title:focus {
    color: @dark-beige;
}

.placement__option {
    display: block;
    float: left;
    width: 126px;
    line-height: 21px;
    color: @basic-dark;
    text-align: start;
//    outline: 2px solid green;
    margin-left: 20px;
//    margin-top: 0;
}

.placement__cost {
    display: block;
    float: right;
    width: 150px;
    line-height: 21px;
//    outline: 2px solid blue;
    color: @basic-dark;
    text-align: start;
}

.placement__button {
    text-transform: uppercase;
    border: none;
    color: @basic-white;
    line-height: 21px;
//    margin-top: 18px;
    padding: 3px 17px;
    margin-top: 10px;
}

.placement__button--more-button {
    background-color: @basic-blue;
}

.placement__button--book-button {
    background-color: @basic-brown;
}

.placement__button--more-button:hover {
    background-color: @deep-blue;
}

.placement__button--more-button:focus {
    color: rgba(256, 256, 256, 0.3);
    background-color: @indigo-blue;
}

.placement__button--book-button:hover {
    background-color: @deep-brown;
}

.placement__button--book-button:focus {
    color: rgba(256, 256, 256, 0.3);
    background-color: @dark-brown;
}

.placement__grade {
//    outline: 2px solid green;
//    float: right;
    text-align: end;
    margin-right: 71px;
}

.placement__stars {
    display: inline-flex;
}

.placement__star {
    position: relative;
    display: block;
    cursor: pointer;
    margin-right: 25px;
}

.placement__star:after {
    content: "";
    position: absolute;
    background-image: url(../img/star.svg);
    width: 19px;
    height: 17px;
    top: 8px;
    left: 8px;
}

.placement__rating {
    line-height: 21px;
    color: @basic-grey;
    background-color: @light-beige;
    margin-top: 65px;
    margin-left: 510px;
    text-align: center;
    padding-top: 3px;
    width: 110px;
    height: 27px;
//    float: right;
}