.offer {
    text-align: center;
    margin: 0;
    margin-top: 60px;
}

.offer__title {
    font-weight: bold;
    font-size: 30px;
    line-height: 24px;
}

.offer__description {
    font-size: 14px;
    line-height: 26px;
    color: @basic-dark;
    margin: 0;
    margin-bottom: 35px;
    position: relative;
}

.offer__button {
    position: relative;
    padding: 30px 125px;
    background-color: @basic-brown;
    border: none;
    text-transform: uppercase;
    
}

.offer__link {
    color: @basic-white;
    font-size: 21px;
    line-height: 26px;
    font-weight: bold;
//    margin: 30px 130px 30px 130px;
}

.offer__button:hover {
    background-color: @deep-brown;
}

.offer__button:focus {
    background-color: @dark-brown;
}