.form-feedback {
    width: 100%;
    text-align: center;
}
.form-feedback__title {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    padding-bottom: 15px;
//    outline: 2px solid red;
}

.form-feedback__input {
    margin-top: 15px;
    margin-bottom: 35px;
    width: 100%;
    background-color: @light-beige;
    border: none;
    padding: 18px 21px 24px 21px;
    font-family: "PT Sans", "Arial", sans-serif;
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;
}

.form-feedback__input::placeholder {
  color: @basic-black;
}

.form-review__comment {
  margin-bottom: 58px;
}
