.sorting {
//    display: flex;
    margin:0;
    padding:0;
}

.sorting__wrapper {
    display: grid;
    grid-template-columns: 200px 130px 270px 600px;
    grid-template-rows: 86px;
//    outline: 5px solid red;
}

.sorting__number-of-options {
    font-size: 21px;
    font-weight: bold;
    padding-left: 45px;
    padding-top: 29px;
    padding-bottom: 31px;
//    text-align: center;
}

.sorting__title {
    font-size: 12px;
    line-height: 18px;
    color: @basic-black;
    padding-top: 20px;
}

.sorting__list {
    list-style: none;
    display: flex;
    justify-content: space-between;
//    margin-left: -10px;
//    text-align: start;
//    outline: 5px solid red;
    padding-left: 0px;
}

.sorting__item {
    display: block;
    width: 80px;
    height: 18px;
    font-size: 12px;
    line-height: 18px;
    color: @basic-black;
    margin-top: 18px;
    opacity: 0.3;
//    box-sizing: border-box;
//    padding-bottom: 0;
//    outline: 5px solid red;
    border-bottom: 1px dashed @basic-blue;
}

.sorting__item:hover {
    color: @basic-blue;
    opacity: 1;
}

.sorting__item:active {
    color: @basic-black;
    opacity: 1;
    border-bottom: none;
}

.sorting__item--current {
    color: @basic-blue;
    opacity: 1;
    border-bottom: none;
}



.sorting__up-down {
    list-style: none;
    display: flex;
//    text-align: end;
    justify-content: flex-end;
    margin-right: 80px;
//    outline: 5px solid red;
}

.sorting__up {
    cursor: pointer;
//    display: flex;
}

sorting__up:hover path {
    fill: @basic-black;
}

.sorting__down {
    position: relative;
    display: block;
    cursor: pointer;
}

.sorting__down:after {
    content: "";
    position: absolute;
    background-image: url(../img/icon-down-dir.svg);
    width: 11px;
    height: 10px;
    top: 8px;
    left: 8px;
}

.sorting__down:after::hover {
    fill:aqua;
}