.infrastructure {
    border: none;
    padding-left: 55px;
}

.infrastructure__title {
    font-size: 16px;
    line-height: 21px;
    font-weight: bold;
    padding-top: 26px;
    margin-left: -10px;
    text-align: start;
}

.infrastructure__list {
    list-style: none;
}

.infrastructure__item {
    margin-bottom: 20px;
    text-align: start;
}

.infrastructure__name-item{
    position: relative;
    display: block;
    cursor: pointer;
    user-select: none;
    margin-left: 0px;
//    text-align: justify;
}

.infrastructure__name-item::before {
    content: "";
    position: absolute;
    left: -45px;
    top: 0;
    width: 23px;
    height: 23px;
    border: 2px solid @basic-white;
    z-index: 1;
}

input:checked + label::after {
    content: "";
    position: absolute;
    top: 0px;
    left: 25px;
    width: 23px;
    height: 18px;
//    background-color: red;
    background-image: url("../img/check.svg");
    background-repeat: no-repeat;
    background-position: 0 0;
    z-index: 2;
}