.benefits {
    text-align: center;
//    min-width: 1200px;
}

.benefits__wrapper {
//    text-align: center;
    width: 1280px;
    min-width: 320px;
    margin: 0 auto;
    padding-left: 40px;
    padding-right: 40px;
}

.benefits__list {
//    margin: 0 auto;
    padding: 0;
    list-style: none;
    display: grid;
    justify-content: center;
    grid-template-areas:
    "benefits__item--true-sity benefits__item--true-sity benefits__item--true-sity"
    "benefits__item--motel benefits__item--eat benefits__item--souvenirs"
    "benefits__item--bridge-of-devil benefits__item--bridge-of-devil benefits__item--bridge-of-devil"
    "benefits__item--little-square benefits__item--nice-road benefits__item--few-tourists";
//    grid-template-columns: 400px 400px 400px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows:
    256px
    330px
    256px
    256px;
}

.benefits__item--true-sity {
    margin: 0;
    padding: 0;
    justify-content: center;
    grid-area: benefits__item--true-sity;
    display: grid;
    grid-template-areas:
    "benefits__information--true-sity benefits__picture--true-sity benefits__picture--true-sity";
//    grid-template-columns: 400px 400px 400px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows:
    256px;
}

.benefits__information--true-sity {
    grid-area: benefits__information--true-sity;
}

.benefits__picture--true-sity{
    grid-area: benefits__picture--true-sity;
//    display: block;
    margin: 0;
    padding: 0;
}

.benefits__picture {
    object-fit: cover;
    height: 256px;
}

