.benefits__item--motel {
    grid-area: benefits__item--motel;
}

.benefits__item--eat {
    grid-area: benefits__item--eat;
}

.benefits__item--souvenirs {
    grid-area: benefits__item--souvenirs;
}

.benefits__item--bridge-of-devil {
    margin: 0;
    padding: 0;
    justify-content: center;
    grid-area: benefits__item--bridge-of-devil;
    display: grid;
    grid-template-areas: 
    "benefits__picture--bridge-of-devil benefits__picture--bridge-of-devil benefits__information--bridge-of-devil";
//    grid-template-columns: 400px 400px 400px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows:
    256px;
}

.benefits__information--bridge-of-devil {
    grid-area: benefits__information--bridge-of-devil;
}

.benefits__picture--bridge-of-devil {
    grid-area: benefits__picture--bridge-of-devil;
    margin: 0;
    padding: 0;
}

.benefits__item--little-square {
    grid-area: benefits__item--little-square;
}

.benefits__item--nice-road {
    grid-area: benefits__item--nice-road;
}

.benefits__item--few-tourists {
    grid-area: benefits__item--few-tourists;
}

.benefits__title {
//    display: block;
    font-weight: bold;
    font-size: 21px;
    line-height: 21px;
//    margin: 0;
    margin-top: 50px;
}

.benefits__number {
//    display: block;
    font-size: 14px;
    line-height: 21px;
    margin-top: 25px;
}

.benefits__description {
    font-size: 14px;
    line-height: 21px;
    margin-top: 25px;
}

.benefits__item:nth-child(1),
.benefits__item:nth-child(5) {
    background-color: @basic-blue;
    color: @basic-white;
}

.benefits__item:nth-child(6),
.benefits__item:nth-child(7),
.benefits__item:nth-child(8){
    background-color: @basic-beige;
}

.benefits__item--motel,
.benefits__item--eat,
.benefits__item--souvenirs {
    position: relative;
}

.benefits__item--motel::before {
  content: "";
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 60px;
  width: 75px;
  height: 72px;
  background-image: url("../img/icon-1.png");
}

.benefits__item--eat::before {
  content: "";
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 60px;
  width: 64px;
  height: 77px;
  background-image: url("../img/icon-2.png");
}

.benefits__item--souvenirs::before {
  content: "";
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 60px;
  width: 75px;
  height: 70px;
  background-image: url("../img/icon-3.png");
}

@media (max-width: @mobile-width-only) {
    .benefits__wrapper {
        width: 100%;
        padding: 0;
    }
    .benefits__list {
        display: flex;
        flex-direction: column;
    }
    .benefits__item {
        display: flex;
        flex-direction: column;
    }
}

@media (min-width: @tablet-width) and (max-width: @desktop-width) {
    .benefits__wrapper {
        width: 100%;
        min-width: 768px;
    }
}