.page-footer {
    text-align: center;
}
.page-footer__wrapper {
//    padding-left: 40px;
//    padding-right: 40px;
//    display: flex;
//    flex-direction: row;
    display: grid;
//    padding: 0 100px;
    padding-left: 80px;
    padding-right: 150px;
    justify-content: center;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 30px;
//    grid-template-columns: 400px 400px 400px;
    height: 120px;
}
@media (max-width: @desktop-width) {
    .page-footer__wrapper {
        width: 100%;
    }
}
@media (max-width: @mobile-width-only) {
    .page-footer__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0;
    min-height: 233px;
    }
}