.gallery {
  padding-top: 22px;
  padding-left: 20px;
  padding-right: 20px;
  line-height: 24px;
}

.gallery__list {
    list-style: none;
    padding: 0;
    margin: 0;
}

@media (min-width: @tablet-width) {
    .gallery {
        padding-left: 60px;
        padding-right: 60px;
    }
    .gallery__list {
        display: grid;
        grid-template-columns: 1fr 1fr;
//        grid-template-rows:
//            auto
//            auto
//            auto;
//        grid-template-areas:
//            "gallery__item--first gallery__item--first"
//            "gallery__item--second gallery__item--third"
//            "gallery__item--fourth gallery__item--second";
    }
    .gallery__item {
        background-color: @light-beige;
    }
    .gallery__likes {
        float: right;
//        margin-right: auto;
//        padding-bottom: 20px;
    }
    .gallery__item--first {
        grid-column: span 2;
//        width: 100%;
    }
//    .gallery__item--second {
//        grid-area: gallery__item--second;
//    }
//    .gallery__item--third {
//        grid-area: gallery__item--third;
//    }
//    .gallery__item--fourth {
//        grid-area: gallery__item--fourth;
//    }
}

@media (min-width: @desktop-width) {
    .gallery {
        width: 1320px;
        margin-left: auto;
        margin-right: auto;
    }
    .gallery__list {
        grid-template-columns: 1fr 1fr 1fr;
    }
    .gallery__item--first {
        grid-column: span 3;
//        width: 100%;
    }
}

.gallery__img {
    width: 100%;
    padding: 0;
    margin: 0;
//    object-fit: cover;
}
.gallery__name {
    margin: 0;
}
.gallery__author {
  text-transform: none;
    margin: 0;
}

.gallery__name strong {
  text-transform: uppercase;
}

.gallery__likes {
    font-weight: bold;
    position: relative;
    padding-left: 37px;
    margin: 0;
}
.gallery__likes::before {
    content: "";
    position: absolute;
    left: 0px;
    top: 0px;
    width: 23px;
    height: 21px;
    background-image: url("../img/like-icon.svg")
//    border-radius: 50%;
//    background-color: @light-beige;
}
